/* article style */

// article breadcrumb
.detail-page {
    .breadcrumb {
        .cate {
            a {
                color: $brand-success;
                border-top-color: $brand-success;
            }
        }
    }
}

.main-article {
    position: relative;
    // padding: 0 10px;
    .article-hdr {
        // header article 
        .article-title {
            @include font-size(35, 45);
            margin-bottom: 5px;
            font-weight: bold;
            margin-top: 0;
        }
        .meta {
            margin-bottom: 5px;
            @include font-size(15, 21);
            color: #888;
            .meta-info {
                .cate-prefix {
                    color: white;
                    background-color: $brand-primary;
                    padding: 2px 3px;
                    margin-right: 3px;
                }
                .cate {}
                .author {
                    text-transform: uppercase;
                    color: $brand-primary;
                    font-weight: bold;
                    &::after {
                        display: inline;
                        content: ' - ';
                    }
                }
                time {
                    &::after {
                        display: inline;
                        content: ' - ';
                    }
                }
                .comment-count::after {
                    margin-left: 3px;
                    display: inline;
                    content: '';
                    font-family: 'fontawesome';
                }
            }
            .meta-share {
                >* {
                    display: inline-block;
                    float: left;
                    margin-left: 5px;
                    vertical-align: bottom !important;
                }
            }
        }
        .summary {
            font-size: 16px;
            font-size: 1.6rem;
            line-height: 1.42857143em;
            margin-bottom: 10px;
            font-weight: bold;
            a {
                color: $brand-primary;
            }
        }
        .related-listing {
            width: 100%;
            margin-right: 0;
            .zone-title {
                background: none;
                margin-bottom: 10px;
                padding-left: 0;
                @include font-size(18, 24);
            }
            .story {
                padding-left: 15px;
                background: url(../images/bullet.png) 0 7px no-repeat;
                padding-bottom: 0;
                border-bottom: none;
                .title {
                    @include font-size(14, 19);
                    margin-bottom: 0;
                    a {
                        color: lighten($brand-success, 10%);
                        @include haf() {
                            color: $brand-primary;
                        }
                    }
                }
            }
        }
    }
    .bottom-share {
        padding-bottom: 10px;
        margin-bottom: 10px;
        .title {
            text-transform: uppercase;
            padding-bottom: 5px;
            font-weight: bold;
        }
        .social {
            display: inline;
            ul {
                li {
                    float: left;
                    margin-right: 5px;
                }
            }
        }
    }
    .article-content {
        //main article content
        .content {
            line-height: 1.42857143em;
            font-size: 16px;
            font-size: 1.6rem;
            p,
            div,
            h3,
            h4,
            h5 {
                margin-bottom: 10px;
                margin-top: 0;
            }
            img {
                max-width: 100%;
            }
            a {
                font-weight: bold;
                color: $brand-primary;
            }
        }
        /* -------------- */
        .full-img {
            display: block;
            width: 100%;
            margin-bottom: 15px;
            img {
                margin-bottom: 5px;
                max-width: 100%;
                height: auto;
                display: block;
                margin-left: auto;
                margin-right: auto;
            }
            .fig {
                color: #555;
                font-style: italic;
                text-align: center;
                display: block;
            }
        }
        /* -------------- */
        .aside-img {
            margin-bottom: 15px;
            p.thumbnail {
                position: relative;
                width: 100%;
                margin-bottom: 0px;
                img {
                    max-width: 100%;
                }
            }
            .fig {
                font-size: 13px;
                color: #222;
                font-style: italic;
            }
            i,
            em {
                position: absolute;
                bottom: 0;
                left: 0;
            }
        }
        .aside-img.pull-right {
            margin-left: 30px;
            margin-right: 0px;
        }
        .aside-img.pull-left {
            margin-right: 30px;
        }
        /* -------------- */
        .article-photo.inlinephoto {
            display: block;
            margin-bottom: 10px;
            img {
                margin-bottom: 5px;
                max-width: 100%;
                height: auto;
                display: block;
                margin-left: auto;
                margin-right: auto;
            }
            span,
            .fig {
                font-size: 12px;
                color: #222;
                font-style: italic;
                display: block;
            }
        }
        /* -------------- */
        .author {
            font-weight: bold;
            text-align: right;
        }
        /* -------------- */
        .inner-article {
            padding-bottom: 0;
            margin-bottom: 20px;
            background: #f0f0f0;
            figure {
                margin-top: 0 !important;
                margin-right: 0 !important;
                img {
                    border-top-right-radius: 0 !important;
                    border-bottom-right-radius: 0 !important;
                    width: 70px;
                    height: 70px;
                }
            }
            header {
                padding: 10px 10px 5px 10px;
                height: 70px;
                overflow: hidden;
                .title {
                    font-size: 12px;
                    font-size: 1.2rem;
                    font-weight: bold;
                    margin-bottom: 0;
                    max-height: 4.2em;
                    overflow: hidden;
                    a {
                        &:hover {
                            color: $brand-primary;
                        }
                    }
                }
            }
            &:hover {
                background: #eee;
            }
        }
        /* video player */
        .video-player {
            margin-left: -10px;
            margin-right: -10px;
        }
        /* table with */
        table {
            max-width: 100% !important;
        }
        /* -------------- */
        .quotes {
            margin: 0 0 10px;
            padding: 10px 10px 10px 40px;
            background: #e8eaf0;
            border: 1px solid #d5dbee;
            position: relative;
            clear: both;
            .author {
                font-style: normal;
                text-align: right;
                font-weight: bold;
                display: block;
                padding-top: 10px;
            }
            &::before {
                content: '';
                font-family: "Fontawesome";
                font-size: 20px;
                font-size: 2rem;
                color: $brand-primary;
                position: absolute;
                top: 10px;
                left: 10px;
            }
        }
        .quotes.pull-left {
            margin-right: 20px;
            width: 50%;
        }
        .quotes.pull-right {
            margin-left: 20px;
            width: 50%;
        }
        /* -------------- */
        .article-poll {
            margin-bottom: 20px;
            border: 1px solid #ddd;
            clear: both;
            div,
            p {
                margin-bottom: 0;
            }
            header {
                font-family: Arial, Helvetica, sans-serif;
                border-bottom: 1px solid #ddd;
                padding: 15px;
                margin-bottom: 10px;
                background: #eee;
                h1 {
                    font-weight: bold;
                    font-size: 20px;
                    margin-bottom: 10px;
                }
                a {
                    font-size: 14px;
                }
            }
            .poll-topic {
                padding: 15px 0;
                margin: 0 15px;
                border-bottom: 1px solid #ddd;
                h3 {
                    font-size: 14px;
                }
                lable {
                    font-weight: normal;
                }
                .multi-answers {
                    label {
                        display: block;
                        margin-left: 15px;
                        margin-bottom: 5px;
                    }
                }
            }
            .person-info {
                padding: 10px 0;
                margin: 0 15px;
                .form-group {
                    width: 40%;
                    float: left;
                    margin-bottom: 10px;
                }
                .form-group:nth-child(2n) {
                    float: right;
                }
            }
            footer {
                padding: 15px;
                background: #eee;
                text-align: right;
                h1 {
                    font-weight: bold;
                    font-size: 20px;
                    margin-bottom: 10px;
                }
                a {
                    font-size: 14px;
                }
                button {
                    margin-left: 15px;
                }
            }
        }
        /* -------------- */
        .box-relate,
        .notification {
            padding: 10px;
            background: #ffd7d3;
            border: 1px dashed #333;
            border-radius: 3px;
            &.pull-left,
            &.floatleft {
                float: left;
                width: 50%;
                margin-right: 10px;
            }
            &.pull-right,
            &.floatright {
                float: right;
                width: 50%;
                margin-left: 10px;
            }
        }
        /* -------------- */
        .article-tag {
            margin-bottom: 16px;
            h3 {
                padding-top: 2px;
                font-size: 14px;
                float: left;
                margin: 0 10px 0 0;
                .sprites-keyword {
                    margin-right: 3px;
                }
            }
            &>div {
                // padding-top: 2px;
                a {
                    color: #333;
                    display: inline-block;
                    padding: 0px 10px;
                    border-radius: 3px;
                    margin: 0px 5px 5px 0px;
                    font-size: 12px;
                    font-size: 1.2rem;
                    font-weight: normal;
                    &:hover {
                        color: #fff;
                        background: $brand-primary;
                    }
                }
            }
        }
        // related inline story
        .related-inline-story {
            margin-bottom: 10px;
            background-color: #f7f7f7;
            height: 137px;
            overflow: hidden;
            .thumb {
                margin-bottom: 0;
            }
            .title {
                padding: 10px;
                @include font-size(14, 19);
                a {
                    color: black;
                    @include haf() {
                        color: $brand-primary;
                    }
                }
            }
        }
        /* -------------- */
        .social-share {
            background-color: #f7f7f7;
            padding: 20px;
            text-align: center;
            margin-bottom: 20px;
            .share-title {
                color: #144583;
                margin-top: 0;
                margin-bottom: 10px;
            }
            .social-listing {
                margin-bottom: 0;
                .fb-send {
                    margin-bottom: 0;
                    background: linear-gradient(#0084ff, #006fff);
                    border-radius: 5px;
                    position: relative;
                    top: -6px;
                    >span {
                        position: relative;
                        top: 2px;
                        margin-top: 2px;
                        vertical-align: baseline !important;
                        display: inline-block;
                    }
                }
            }
        }
        .article-comments {
            > header {
                .comments-hdr {
                    margin: 0 0 10px;
                    @include font-size(20, 26);
                    font-weight: bold;
                    color: $brand-success;
                }
            }
            .box-user-comm {
                margin-bottom: 10px;
                .commentlist {}
                .box {
                    margin-bottom: 10px;
                    padding-bottom: 10px;
                    border-bottom: 1px solid #ddd;
                    &:last-child {
                        padding-bottom: 0;
                        border-bottom: none;
                    }
                    .name {
                        margin-top: 0;
                        margin-bottom: 5px;
                        text-transform: uppercase;
                        font-weight: bold;
                        time {
                            text-transform: none;
                            font-weight: normal;
                            color: #777;
                            margin-left: 10px;
                            font-size: 0.8em;
                        }
                        .like,
                        .reply {
                            display: inline-block;
                            margin-left: 3px;
                            font-size: 14px;
                            text-transform: none;
                        }
                        .like {
                            &::before {
                                content: '';
                                font-family: 'fontawesome';
                                display: inline-block;
                                margin-right: 4px;
                            }
                            &.done {
                                color: #bbb;
                                &::before {
                                    content: '';
                                }
                            }
                        }
                    }
                    .text {
                        margin-bottom: 3px;
                        font-size: 15px;
                    }
                    .text-reply {
                        font-size: 14px;
                        color: #777;
                        .box {
                            padding-left: 15px;
                            border-left: 2px solid #aaa;
                        }
                    }
                }
            }
            .box-input {
                position: relative;
                input.form-control {
                    width: 317px;
                    margin-right: 10px;
                    float: left;
                    margin-bottom: 10px;
                    &:nth-child(2) {
                        float: right;
                        margin-right: 0;
                    }
                }
                textarea.form-control {
                    width: 100%;
                }
                #replyfor {
                    >span {
                        font-weight: bold;
                        margin-bottom: 3px;
                    }
                    .closereplyfor {
                        float: right;
                    }
                    #replycontent {
                        font-weight: normal;
                    }
                }
                .action {
                    @include clearfix;
                    margin-top: 10px;
                    >span {
                        color: #777;
                        font-size: 14px;
                        font-size: 1.4rem;
                    }
                    .btn {
                        margin-left: 10px;
                    }
                }
            }
            .view-more {
                text-align: center;
                background-color: #ddd;
                @include font-size(16, 30);
                a {
                    display: block;
                }
            }
            .view-more-plus {
                margin-left: 5px;
                color: #aaa;
                font-size: .8em;
                @include haf() {
                    color: $brand-primary;
                }
            }
        }
    }
}

// style reset boostrap button & form-control
.form-control {
    &:focus {
        box-shadow: none;
        -webkit-box-shadow: none;
        border-color: $brand-success;
    }
}

.btn {}

.btn-warning {
    background: $brand-success;
    border-color: darken($brand-success, 5%);
    @include transition(all, 0.2, linear);
    &:hover {
        background: darken($brand-success, 8%);
        border-color: darken($brand-success, 13%);
    }
    &:active,
    &:focus {
        background: darken($brand-success, 11%);
        border-color: darken($brand-success, 13%);
    }
}


/* same category  */

.main-article {
    & + .zone {
        width: initial;
        float: none;
        margin-top: 10px;
    }
}
